"use client";

import { createContext, useContext, useState, useEffect, ReactNode } from "react";

interface User {
  name: string;
  email: string;
  balance?: number;
}

interface AuthContextType {
  user: User | null;
  login: (userData: User) => void;
  logout: () => void;
  addFunds: (amount: number) => void;
  subtractFunds: (amount: number) => boolean; // retorna false se saldo insuficiente
}

const AuthContext = createContext<AuthContextType>({
  user: null,
  login: () => {},
  logout: () => {},
  addFunds: () => {},
  subtractFunds: () => false,
});

export const AuthProvider = ({ children }: { children: ReactNode }) => {
  const [user, setUser] = useState<User | null>(null);

  // Restaura usu√°rio do localStorage
  useEffect(() => {
    const savedUser = localStorage.getItem("user");
    if (savedUser) {
      setUser(JSON.parse(savedUser));
      console.log("üîπ Sess√£o restaurada:", JSON.parse(savedUser));
    }
  }, []);

  const login = (userData: User) => {
    setUser(userData);
    localStorage.setItem("user", JSON.stringify(userData));
    console.log("‚úÖ Sess√£o atualizada:", userData);
  };

  const logout = () => {
    setUser(null);
    localStorage.removeItem("user");
    console.log("üîπ Logout realizado");
  };

  const addFunds = (amount: number) => {
    if (!user) return;
    const updatedUser = { ...user, balance: (user.balance || 0) + amount };
    setUser(updatedUser);
    localStorage.setItem("user", JSON.stringify(updatedUser));
    console.log(`üí∞ Adicionados ‚Ç¨${amount.toFixed(2)} ao usu√°rio`, updatedUser);
  };

  const subtractFunds = (amount: number) => {
    if (!user) return false;
    if ((user.balance || 0) < amount) return false; // saldo insuficiente
    const updatedUser = { ...user, balance: (user.balance || 0) - amount };
    setUser(updatedUser);
    localStorage.setItem("user", JSON.stringify(updatedUser));
    console.log(`üí∏ Subtra√≠dos ‚Ç¨${amount.toFixed(2)} do usu√°rio`, updatedUser);
    return true;
  };

  return (
    <AuthContext.Provider value={{ user, login, logout, addFunds, subtractFunds }}>
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = () => useContext(AuthContext);
