"use client";

import { useState } from "react";
import { useAuth } from "@/lib/AuthContext";
import { PaymentIcon } from "react-svg-credit-card-payment-icons";

const predefinedAmounts = [5, 10, 20, 50];

export default function AddFundsPage() {
  const { user, addFunds } = useAuth();
  const [selectedAmount, setSelectedAmount] = useState<number | null>(null);
  const [step, setStep] = useState<"selectAmount" | "payment">("selectAmount");
  const [paymentMethod, setPaymentMethod] = useState<"card" | "paypal">("card");
  const [cardNumber, setCardNumber] = useState("");
  const [cardExpiry, setCardExpiry] = useState("");
  const [cardCVV, setCardCVV] = useState("");
  const [paypalEmail, setPaypalEmail] = useState("");
  const [loading, setLoading] = useState(false);

  const handleProceedToPayment = () => {
    if (!selectedAmount) return alert("Escolha um valor primeiro!");
    setStep("payment");
  };

  const handleAddFunds = async () => {
    if (!user) return alert("Voc√™ precisa estar logado.");

    if (paymentMethod === "card") {
      if (cardNumber.length < 12) return alert("N√∫mero de cart√£o inv√°lido!");
      if (!/^\d{2}\/\d{2}$/.test(cardExpiry)) return alert("Data de validade inv√°lida (MM/AA)!");
      if (cardCVV.length < 3) return alert("CVV inv√°lido!");
    }

    if (paymentMethod === "paypal" && !paypalEmail.includes("@")) {
      return alert("Email PayPal inv√°lido!");
    }

    setLoading(true);
    try {
      addFunds(selectedAmount!);
      alert(`‚Ç¨${selectedAmount!.toFixed(2)} adicionados √† sua conta!`);
      setSelectedAmount(null);
      setCardNumber("");
      setCardExpiry("");
      setCardCVV("");
      setPaypalEmail("");
      setStep("selectAmount");
    } catch (error) {
      console.error("Erro ao adicionar fundos:", error);
      alert("Ocorreu um erro. Tente novamente.");
    }
    setLoading(false);
  };

  const paymentIcons = {
    card: <PaymentIcon type="card" className="w-6 h-6 mr-2" />,
    paypal: <PaymentIcon type="paypal" className="w-6 h-6 mr-2" />,
  };

  return (
    <div className="min-h-screen bg-gray-900 text-white flex flex-col items-center justify-center px-6 py-12">
      <h1 className="text-4xl font-bold mb-8">Adicionar Fundos</h1>

      <div className="bg-gray-800 p-8 rounded-xl shadow-lg w-full max-w-md space-y-6">
        {step === "selectAmount" && (
          <>
            <h2 className="text-xl font-semibold mb-2">Escolha um valor</h2>
            <div className="flex gap-3 flex-wrap">
              {predefinedAmounts.map((amount) => (
                <button
                  key={amount}
                  onClick={() => setSelectedAmount(amount)}
                  className={`px-5 py-2 rounded-lg font-semibold transition ${
                    selectedAmount === amount
                      ? "bg-blue-600 text-white"
                      : "bg-gray-700 text-gray-300 hover:bg-gray-600"
                  }`}
                >
                  ‚Ç¨{amount}
                </button>
              ))}
            </div>
            <button
              onClick={handleProceedToPayment}
              disabled={!selectedAmount}
              className="w-full bg-blue-600 hover:bg-blue-700 px-5 py-3 rounded-lg font-bold text-lg transition disabled:opacity-50 mt-4"
            >
              Avan√ßar para Pagamento
            </button>
          </>
        )}

        {step === "payment" && (
          <>
            <h2 className="text-xl font-semibold mb-2">M√©todo de pagamento</h2>
            <div className="flex justify-between gap-4 mb-4">
              {(["card", "paypal"] as const).map((method) => (
                <button
                  key={method}
                  onClick={() => setPaymentMethod(method)}
                  className={`flex items-center flex-1 px-4 py-3 rounded-lg font-semibold transition gap-2 ${
                    paymentMethod === method
                      ? "bg-blue-600 text-white"
                      : "bg-gray-700 text-gray-300 hover:bg-gray-600"
                  }`}
                >
                  {paymentIcons[method]}
                  {method === "card" ? "Cart√£o" : "PayPal"}
                </button>
              ))}
            </div>

            {paymentMethod === "card" && (
              <>
                <input
                  type="text"
                  placeholder="N√∫mero do cart√£o"
                  value={cardNumber}
                  onChange={(e) => setCardNumber(e.target.value)}
                  className="w-full px-3 py-2 rounded-lg bg-gray-700 text-white focus:outline-none mb-2"
                />
                <input
                  type="text"
                  placeholder="Validade (MM/AA)"
                  value={cardExpiry}
                  onChange={(e) => setCardExpiry(e.target.value)}
                  className="w-full px-3 py-2 rounded-lg bg-gray-700 text-white focus:outline-none mb-2"
                />
                <input
                  type="text"
                  placeholder="CVV"
                  value={cardCVV}
                  onChange={(e) => setCardCVV(e.target.value)}
                  className="w-full px-3 py-2 rounded-lg bg-gray-700 text-white focus:outline-none mb-4"
                />
              </>
            )}

            {paymentMethod === "paypal" && (
              <input
                type="email"
                placeholder="Email do PayPal"
                value={paypalEmail}
                onChange={(e) => setPaypalEmail(e.target.value)}
                className="w-full px-3 py-2 rounded-lg bg-gray-700 text-white focus:outline-none mb-4"
              />
            )}

            <button
              onClick={handleAddFunds}
              disabled={loading}
              className="w-full bg-blue-600 hover:bg-blue-700 px-5 py-3 rounded-lg font-bold text-lg transition disabled:opacity-50"
            >
              {loading ? "Processando..." : `Adicionar ‚Ç¨${selectedAmount?.toFixed(2)}`}
            </button>

            <button
              onClick={() => setStep("selectAmount")}
              className="w-full mt-2 bg-gray-700 hover:bg-gray-600 px-5 py-2 rounded-lg text-white font-semibold"
            >
              Voltar
            </button>
          </>
        )}
      </div>
    </div>
  );
}
